import React, {useState} from 'react';
import LineGraph from '../components/lineGraph';
import styled from "styled-components";
import {useStore} from "../stores/common";
import {GraphDataType} from "../types";
import moment from "moment";
import ReactDraggable, {DraggableEventHandler} from 'react-draggable';

const Container = styled.div`
  position: fixed;
  top: 80px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px;
  z-index: 9;
`;

const ColumnWrapper = styled.div`
  background-color: #f8f8f8;
  position: absolute;
  right: 0;
  top: 0;
  width: 600px;
  height: 300px;
  padding-bottom: 10px;
  border: 2px solid gainsboro;
  border-radius: 10px 10px 10px 10px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
`;

const Header = styled.div`
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 24px;
  padding: 10px;
  background-color: #e8e8e8;
  color: #888;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: move;

  svg.icon {
    height: 16px;
    width: 16px;
    padding: 8px;
    fill: #666;
  }
`;

export interface DailyLightSensorPanelProps {

    city: string | null;
    requestUpdate: () => void;

    [key: string]: any;

}

const DailyLightSensorPanel = ({
                                   city,
                                   requestUpdate,
                                   ...rest
                               }: DailyLightSensorPanelProps) => {

    const setCommonStore = useStore(state => state.set);
    const viewState = useStore(state => state.viewState);
    const sensorData = useStore(state => state.dailyLightSensorData);
    const now = new Date(useStore(state => state.world.date));
    const [curPosition, setCurPosition] = useState({
        x: isNaN(viewState.dailyLightSensorPanelX) ? 0 : viewState.dailyLightSensorPanelX,
        y: isNaN(viewState.dailyLightSensorPanelY) ? 0 : viewState.dailyLightSensorPanelY
    });

    const responsiveHeight = 100;

    const onDrag: DraggableEventHandler = (e, ui) => {
        setCurPosition({x: ui.x, y: ui.y});
    };

    const onDragEnd: DraggableEventHandler = (e, ui) => {
        setCommonStore(state => {
            state.viewState.dailyLightSensorPanelX = ui.x;
            state.viewState.dailyLightSensorPanelY = ui.y;
        });
    };

    const closePanel = () => {
        setCommonStore((state) => {
            state.viewState.showDailyLightSensorPanel = false;
        });
        requestUpdate();
    };

    return (
        <ReactDraggable
            handle={'.handle'}
            bounds={'parent'}
            axis='both'
            position={curPosition}
            onDrag={onDrag}
            onStop={onDragEnd}
        >
            <Container>
                <ColumnWrapper>
                    <Header className='handle'>
                        <span>Light Sensor: {city} | {moment(now).format('MM/DD')}</span>
                        <span style={{cursor: 'pointer'}}
                              onTouchStart={() => {
                                  closePanel();
                              }}
                              onMouseDown={() => {
                                  closePanel();
                              }}>
                            Close
                        </span>
                    </Header>
                    <LineGraph
                        type={GraphDataType.DailyRadiationSensorData}
                        dataSource={sensorData}
                        height={responsiveHeight}
                        labelX={'Hour'}
                        labelY={'Radiation'}
                        unitY={'kWh/m/day'}
                        yMin={0}
                        curveType={'linear'}
                        fractionDigits={2}
                        symbolCount={24}
                        referenceX={now.getHours()}
                        {...rest}
                    />
                </ColumnWrapper>
            </Container>
        </ReactDraggable>
    );

};

export default DailyLightSensorPanel;
